import { HashRouter, Switch, Route } from "react-router-dom";
import "../assets/scss/main.scss";
import { animateScroll as scroll } from "react-scroll";
import { StickyContainer, Sticky } from "react-sticky";
import classNames from "classnames";
import Header from "./Header/Header";
import Footer from "./FooterComponent";
import MainContent from "./MainContent";
import AuthModal from "./Modals/AuthModal";
import PersonalArea from "./Pages/PersonalArea/PersonalArea";
import Sales from "./Pages/SalesArea";
import Legal from "./Pages/Legal/Legal";
import About from "./Pages/About/About";
import Restaurants from "./Pages/Restaurants/Restaurants";
import Basket from "./Pages/BasketArea";
import Payment from "./Pages/PaymentArea/PaymentArea";
import Status from "./Pages/StatusArea";
import Constructor from "./Pages/Constructor/Constructor";
import MobileAuth from "./Pages/MobileAuthArea";
import { fbPixel, getRequest, setCartPrice, getCookie, setCookie, getCart, getTotalPrice, getTotalCount, removeItemFromCart, addItemToCart, removeAllItemsFromCart, loadCart, deleteCookie } from "./helpers";

class App extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            categories: [],
            cart: getCart(),
            cartPrice: 0,
            cartCount: 0,
            authorization: {
                show: false,
                authorized: getCookie("authToken"),
            },
            profile: {},
            showMobileAuth: false,
            definitions: {
                layout: {
                    company_phone: "",
                    restaurants: [],
                    social_links: {},
                    frontpage_slider: [],
                    mobile_slider: [],
                },
                orders: {
                    min_amount_for_delivery: 0,
                    bonus_accrual_percent: 0,
                },
            },
            recommendations: [],
            navigateRequest: "",
        };
    }

    componentDidMount() {
        this.priceCart();
    }

    async componentWillMount() {
        const self = this;
        const categories = {};
        const state = { recommendations: [], };

        window.addEventListener("hashchange", this.onHashChange.bind(this), false);

        if (self.state.authorization.authorized) {
            state.profile = await self.getUserProfile();
        } else {
            self.onHashChange();
        }

        await getRequest("/definitions")
            .then((res) => {
                if (res && res.data && res.data.success) {
                    state.definitions = res.data.result;
                }
            });

        await getRequest("/products/featured", {})
            .then((res) => {
                if (res && res.data && res.data.success) {
                    state.recommendations = res.data.result.items;
                }
            });

        await getRequest("/product-categories")
            .then((res) => {
                state.categories = [{
                    id: 0,
                    title: "Хиты",
                    products: state.recommendations,
                }, ...res.data.result.items];
            });

        await this.setState(state);
    }

    componentWillUnmount() {
        window.removeEventListener("hashchange", this.onHashChange.bind(this), false);
    }

    onHashChange() {
        if (!this.state.authorization.authorized) {
            const hash = window.location.hash;
            if (hash === "#/cabinet" || hash === "#/payment") {
                window.location.hash = "#/";

                return;
            }
        }

        if (this.state.showMobileAuth) {
            this.setState({ showMobileAuth: false, });
        }

        setTimeout(() => {
            scroll.scrollToTop();
        }, 100);
    }

    addToCart = (product) => {
        this.setState({ cart: addItemToCart(product), }, () => {
            this.priceCart();
        });
        fbPixel("micro");
    }

    removeFromCart(product) {
        this.setState({ cart: removeItemFromCart(product), }, () => {
            this.priceCart();
        });
    }

    removeAllItemsFromCart(product) {
        this.setState({ cart: removeAllItemsFromCart(product), }, () => {
            this.priceCart();
        });
    }

    loadCart(items) {
        loadCart(items);

        this.setState({ cart: loadCart(items), }, () => {
            this.priceCart();
        });
    }

    priceCart() {
        this.setState({
            cartPrice: getTotalPrice(),
            cartCount: getTotalCount(),
        }, () => { setCartPrice(); });
    }

    async getUserProfile() {
        const self = this;
        const token = getCookie("authToken");
        let profile = {};

        if (token) {
            await getRequest("/profile?addresses=true&orders=true&cards=true")
                .then((res) => {
                    if (res && res.data && res.data.success === true) {
                        profile = res.data.result;
                    }
                });
        }

        return profile;
    }

    logout() {
        deleteCookie("authToken");
        this.setState({ profile: {}, }, () => {
            window.location.hash = "#/";
        });
    }

    openAuth() {
        const authorization = Object.assign({}, this.state.authorization);

        authorization.authorized = "";
        authorization.show = true;

        this.setState({ authorization, });
    }

    closeAuth = () => {
        const authorization = Object.assign({}, this.state.authorization);

        authorization.authorized = getCookie("authToken");
        authorization.show = false;

        this.setState({ authorization, navigateRequest: "", });
    }

    doAuth(data) {
        if (data && !!data.token) {
            setCookie("authToken", data.token, {
                path: window.location.pathname,
                expires: 2550259188,
            });

            const authorization = Object.assign({}, this.state.authorization);
            authorization.authorized = data.token;
            authorization.show = false;

            if (authorization.authorized && this.state.navigateRequest) {
                this.doNavigate(this.state.navigateRequest);
            }

            this.setState({ authorization, navigateRequest: "", }, async () => {
                const profile = await this.getUserProfile();
                this.setUserProfile(profile);
            });
        }
    }

    setUserProfile(profile) {
        this.setState({ profile, });
    }

    setProfileBonuses(bonuses) {
        const profile = Object.assign({}, this.state.profile);

        profile.bonuses = bonuses;
        this.setState({ profile, });
    }

    showUserProfile() {
        this.onNavigateRequest("#/cabinet");
    }

    doNavigate(link) {
        if (this.state.showMobileAuth) {
            this.setState({ showMobileAuth: !this.state.showMobileAuth, });
        }
        window.location.href = link;
    }

    onNavigateRequest(requestLink, unauthorized) {
        if (this.state.profile.phone || unauthorized === true) {
            this.doNavigate(requestLink);
        } else {
            this.setState({ navigateRequest: requestLink, });
            this.openAuth();
        }
    }

    setShowMobileMenu() {
        this.setState({ showMobileAuth: !this.state.showMobileAuth, });
    }

    setAddresses(addresses) {
        const profile = Object.assign({}, this.state.profile);

        profile.addresses = addresses;
        this.setState({ profile, });
    }

    setProfileEmail(email, email_confirmed, email_subscribed) {
        const profile = Object.assign({}, this.state.profile);

        profile.email = email;
        profile.email_confirmed = email_confirmed;
        profile.email_subscribed = email_subscribed;
        this.setState({ profile, });
    }

    get mobileAuth() {
        return (
            <MobileAuth
                onShowUserProfile={this.showUserProfile.bind(this)}
                profile={this.state.profile}
            />
        );
    }

    render() {
        return (
            <StickyContainer>
                <Sticky>
                    {({ style, isSticky, }) => (
                        <div
                            className={classNames({ "sticky-header": isSticky, })}
                            style={{
                                zIndex: "11000",
                                backgroundColor: "#fff",
                                ...style,
                            }}
                        >
                            <AuthModal
                                modalTitle="Авторизация"
                                modalText="Укажите телефон, куда прислать СМС с кодом для авторизации"
                                modalDescription="Туда же будут начислены побонусы"
                                requestAuthCodeApi="/auth/login-or-register"
                                confirmAuthCodeApi="/auth/dual-confirm"
                                isOpen={this.state.authorization.show && !this.state.authorization.authorized}
                                onClose={this.closeAuth}
                                onSuccess={this.doAuth.bind(this)}
                            />

                            <Header
                                categories={this.state.categories}
                                cartPrice={this.state.cartPrice}
                                profile={this.state.profile}
                                category={this.state.category}
                                setShowMobileMenu={this.setShowMobileMenu.bind(this)}
                                showMobileAuth={this.state.showMobileAuth}
                                onShowUserProfile={this.showUserProfile.bind(this)}
                                menuItemClick={(id) => {
                                    window.location.href = "#/";
                                }}
                            />
                        </div>
                    )
                    }
                </Sticky>
                {this.state.showMobileAuth ? this.mobileAuth : ""}
                <HashRouter>
                    <Switch>
                        <Route
                            path="/"
                            exact
                            component={() => (
                                <MainContent
                                    cartPrice={this.state.cartPrice}
                                    cartCount={this.state.cartCount}
                                    cart={this.state.cart}
                                    addToCart={this.addToCart}
                                    removeFromCart={this.removeFromCart.bind(this)}
                                    recommendations={this.state.recommendations}
                                    slides={this.state.definitions.layout.frontpage_slider}
                                    mobileSlides={this.state.definitions.layout.mobile_slider}
                                    showMobileAuth={this.state.showMobileAuth}
                                    categories={this.state.categories}
                                />
                            )}
                        />
                        <Route
                            path="/cabinet"
                            component={() => (
                                <PersonalArea
                                    profile={this.state.profile}
                                    showMobileAuth={this.state.showMobileAuth}
                                    logout={this.logout.bind(this)}
                                    setProfileEmail={this.setProfileEmail.bind(this)}
                                    setAddresses={this.setAddresses.bind(this)}
                                    getUserProfile={this.getUserProfile.bind(this)}
                                    setUserProfile={this.setUserProfile.bind(this)}
                                    loadCart={this.loadCart.bind(this)}
                                    dadataToken={this.state.definitions.dadataToken}
                                />
                            )}
                        />
                        <Route path="/sales" component={() => <Sales showMobileAuth={this.state.showMobileAuth} />} />
                        <Route path="/legal" component={() => <Legal showMobileAuth={this.state.showMobileAuth} />} />
                        <Route path="/about" component={() => <About showMobileAuth={this.state.showMobileAuth} />} />
                        <Route
                            path="/restaurants"
                            component={() => (
                                <Restaurants
                                    restaurants={this.state.definitions.layout.restaurants}
                                    showMobileAuth={this.state.showMobileAuth}
                                />
                            )}
                        />
                        <Route
                            path="/basket"
                            component={() => (
                                <Basket
                                    pageTitle="Pobo - Корзина"
                                    recommendations={this.state.recommendations}
                                    min_amount_for_delivery={this.state.definitions.orders.min_amount_for_delivery}
                                    showMobileAuth={this.state.showMobileAuth}
                                    onNavigateRequest={this.onNavigateRequest.bind(this)}
                                    addToCart={this.addToCart.bind(this)}
                                    removeFromCart={this.removeFromCart.bind(this)}
                                    removeAllItemsFromCart={this.removeAllItemsFromCart.bind(this)}
                                />
                            )}
                        />
                        <Route
                            path="/payment"
                            component={() => (
                                <Payment
                                    showMobileAuth={this.state.showMobileAuth}
                                    bonus_accrual_percent={this.state.definitions.orders.bonus_accrual_percent}
                                    restaurants={this.state.definitions.layout.restaurants}
                                    profile={this.state.profile}
                                    isAuthorized={this.state.authorization.authorized}
                                    setUserProfile={this.setUserProfile.bind(this)}
                                    setAddresses={this.setAddresses.bind(this)}
                                    setProfileBonuses={this.setProfileBonuses.bind(this)}
                                    loadCart={this.loadCart.bind(this)}
                                    priceCart={this.priceCart.bind(this)}
                                    doAuth={this.doAuth.bind(this)}
                                    cloudpaymentsPublicId={this.state.definitions.cloudpaymentsPublicId}
                                    dadataToken={this.state.definitions.dadataToken}
                                />
                            )}
                        />
                        <Route
                            path="/constructor"
                            component={() => (
                                <Constructor
                                    addToCart={this.addToCart.bind(this)}
                                    showMobileAuth={this.state.showMobileAuth}
                                />
                            )}
                        />
                        <Route
                            path="/status/:id"
                            component={() => (
                                <Status
                                    profile={this.state.profile}
                                    showMobileAuth={this.state.showMobileAuth}
                                    loadCart={this.loadCart.bind(this)}
                                />
                            )}
                        />
                        <Route
                            path="/status/"
                            component={() => (
                                <Status
                                    profile={this.state.profile}
                                    showMobileAuth={this.state.showMobileAuth}
                                    loadCart={this.loadCart.bind(this)}
                                />
                            )}
                        />
                    </Switch>
                </HashRouter>
                <Footer
                    showMobileAuth={this.state.showMobileAuth}
                    companyPhone={this.state.definitions.layout.company_phone}
                    socialLinks={this.state.definitions.layout.social_links}
                    authorized={this.state.authorization.authorized}
                    onNavigateRequest={this.onNavigateRequest.bind(this)}
                />
            </StickyContainer>
        );
    }
}
export default App;
