import React from "react";
import Button from "./ButtonComponent";
import AddItemIcon from "./icons/AddItem";
import RemoveItemIcon from "./icons/RemoveItem";
import { formatCurrency } from "./helpers";

class MarketItem extends React.Component {
    constructor(props) {
        super(props);
        this.state = { elementInCart: this.props.elementInCart, };
    }

    renderItemControls(elementInCart) {
        if (elementInCart === 0) {
            return (
                <Button
                    buttonType="default"
                    buttonTitle="В корзину"
                    buttonClass="buy-item"
                    onClickMethod={() => {
                        this.props.addToCart(this.props.product);
                        this.setState({ elementInCart: this.state.elementInCart + 1, });
                    }}
                    dataObject={this.props.product}
                />
            );
        }
        return (
            <div className="item-controls-cart">
                <div
                    className="remove-item-icon"
                    onClick={() => {
                        this.props.removeFromCart(this.props.product);
                        this.setState({ elementInCart: this.state.elementInCart - 1, });
                    }}
                >
                    <RemoveItemIcon />
                </div>
                {elementInCart}
                <div
                    className="add-item-icon"
                    onClick={() => {
                        this.props.addToCart(this.props.product);
                        this.setState({ elementInCart: this.state.elementInCart + 1, });
                    }}
                >
                    <AddItemIcon />
                </div>
            </div>
        );
    }

    renderItemLabel(label) {
        switch (label.icon) {
        case "circle":
            return (
                <svg key={label.icon} width="16" height="16.38" viewBox="0 0 16 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M8 0.5C3.65714 0.5 0 4.15714 0 8.5C0 12.8429 3.65714 16.5 8 16.5C12.3429 16.5 16 12.8429 16 8.5C16 4.15714 12.3429 0.5 8 0.5ZM8 15.1286C4.34286 15.1286 1.37143 12.1571 1.37143 8.5C1.37143 4.84286 4.34286 1.87143 8 1.87143C11.6571 1.87143 14.6286 4.84286 14.6286 8.5C14.6286 12.1571 11.6571 15.1286 8 15.1286Z" fill="#8DD161" />
                </svg>
            );
        case "fire":
            return (
                <svg key={label.icon} width="16" height="16.38" viewBox="0 0 16 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M10.2897 16.7838C10.0613 16.7838 9.83292 16.7838 9.83292 16.5555C9.83292 16.3273 9.83292 16.099 9.83292 15.8707C10.5182 15.186 10.7466 14.2729 10.5182 13.3599C10.5182 12.6751 10.0613 11.7621 9.3761 11.0773L8.00564 9.70777L6.63518 11.0773C5.94995 11.7621 5.49313 12.6751 5.26472 13.3599C5.03631 14.2729 5.26472 15.186 5.94995 15.8707C6.17836 16.099 6.17836 16.3273 5.94995 16.5555C5.94995 16.7838 5.49313 16.7838 5.26472 16.7838C1.83856 15.186 0.0112795 12.9034 0.0112795 10.1643C0.0112795 9.93603 -0.217131 7.88171 1.38174 6.05564C2.7522 4.45783 5.03631 2.40351 5.03631 2.40351C5.26472 2.17525 5.72154 2.17525 5.94995 2.40351L8.46246 4.91435C8.69087 5.14261 8.69087 5.59912 8.46246 5.82738C8.23405 6.05564 7.77723 6.05564 7.54882 5.82738L5.49313 3.5448C4.8079 4.22957 3.20902 5.82738 2.29538 6.74041C1.15333 8.33822 1.15333 9.93603 1.15333 9.93603C1.15333 11.9904 2.52379 13.5882 3.89425 14.5012C3.66584 14.0447 3.66584 13.3599 3.89425 12.9034C4.12266 11.9904 4.57949 11.0773 5.49313 10.1643L7.32041 8.33822C7.54882 8.10996 7.54882 8.10996 7.77723 8.10996C8.00564 8.10996 8.00564 8.10996 8.23405 8.33822L10.0613 10.1643C10.975 11.0773 11.4318 11.9904 11.6602 12.9034C11.6602 13.5882 11.6602 14.0447 11.6602 14.5012C13.2591 13.5882 14.4011 11.9904 14.4011 9.93603C14.4011 9.93603 14.6295 8.33822 13.2591 6.74041C12.3454 5.37086 8.23405 1.49048 8.23405 1.49048C8.00564 1.26222 8.00564 0.805702 8.23405 0.577444C8.46246 0.349185 8.69087 0.349185 8.91928 0.577444C9.14769 0.577444 13.2591 4.45783 14.6295 6.05564C16 7.88171 16 9.70777 16 10.1643C16 12.9034 14.1727 15.186 10.7466 16.7838C10.5182 16.7838 10.5182 16.7838 10.2897 16.7838Z" fill="#F8819C" />
                </svg>
            );
        case "leaf":
            return (
                <svg key={label.icon} width="16" height="16.38" viewBox="0 0 16 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M13.6629 13.3485C14.8614 11.8939 15.5805 10.197 15.5805 8.25758C15.5805 3.89394 12.2247 0.5 7.91012 0.5H0.719109C0.479408 0.5 0 0.742424 0 1.22727V8.5C0 12.3788 3.3558 16.0152 7.67041 16.0152C9.58801 16.0152 11.2659 15.2879 12.7041 14.0758L14.8614 16.2576C15.1011 16.5 15.1011 16.5 15.3408 16.5C15.5805 16.5 15.5805 16.5 15.8202 16.2576C16.0599 16.0152 16.0599 15.5303 15.8202 15.2879L13.6629 13.3485ZM7.67041 14.803C4.0749 14.803 1.1985 11.8939 1.1985 8.25758V1.71212H7.67041C11.2659 1.71212 14.1423 4.62121 14.1423 8.25758C14.1423 9.71212 13.6629 11.1667 12.7041 12.3788L5.51312 5.10606C5.27342 4.86364 4.79401 4.86364 4.55431 5.10606C4.31461 5.34848 4.31461 5.83333 4.55431 6.07576L11.7453 13.3485C10.5468 14.0758 9.10861 14.803 7.67041 14.803Z" fill="#8DD161" />
                </svg>
            );
        }

        return "";
    }

    renderItemSpecificationsDesktop(specifications) {
        return specifications.map((item, i) => (<div key={"spec" + i} className="text-description item-desc-small">{item.value} {item.title}</div>));
    }

    renderItemSpecificationsMobile(specifications) {
        const simpleItem = {
            value: specifications[0].value,
            title: specifications[0].title,
            text: `${specifications[0].value} ${specifications[0].title}`,
        };

        const complexItem = {
            value: [],
            title: [],
            text: "",
        };

        for (let i = 1; i < specifications.length; i++) {
            complexItem.title.push(specifications[i].title.substring(0, 1).toUpperCase());
            complexItem.value.push(specifications[i].value);
        }

        complexItem.title = complexItem.title.join("/");
        complexItem.value = complexItem.value.join("/");
        complexItem.text = `${complexItem.title}: ${complexItem.value}`;

        return [simpleItem, complexItem].map((item, i) => (
            <div key={"spec" + i} className="text-description item-desc-small">{item.text}</div>
        ));
    }

    render() {
        const itemControls = this.renderItemControls(this.state.elementInCart);
        let itemLabelText = "";
        let itemLabelIcons = "";
        if (this.props.product.labels && this.props.product.labels.length) {
            itemLabelText = this.props.product.labels.map(label => label.title).join(", ");
            itemLabelIcons = this.props.product.labels.map(label => this.renderItemLabel(label));
        }

        const specifications = this.props.product.specifications;
        let itemSpecificationsDesktop = "";
        let itemSpecificationsMobile = "";

        if (specifications && specifications.length) {
            itemSpecificationsDesktop = this.renderItemSpecificationsDesktop(specifications);
            itemSpecificationsMobile = this.renderItemSpecificationsMobile(specifications);
        }

        return (
            <div key={`product${this.props.product.id}`} className="item">
                <div className="item__content">
                    <div className="item-img-container">
                        <img className="item-img-container__img" src={this.props.product.image} />
                    </div>
                    <div className="item-title">
                        <div className="item-title__title">
                            {this.props.product.title}
                            <div className="item-title__icon" title={itemLabelText}>{itemLabelIcons}</div>
                        </div>
                    </div>
                    <div className="item-desc">
                        <div className="item-desc__description text-description">
                            {this.props.product.description}
                        </div>
                    </div>
                </div>
                <div className="item__footer">
                    <div className="item-props">
                        {itemSpecificationsDesktop}
                    </div>
                    <div className="item-props--mobile">
                        {itemSpecificationsMobile}
                    </div>
                    <div className="item-controls">
                        {itemControls}
                        <div className="price-container">{formatCurrency(this.props.product.price)}</div>
                    </div>
                </div>
            </div>
        );
    }
}

export default MarketItem;
