import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import AboutItem from "./AboutItem";
import { getRequest } from "../../helpers";
import SliderItem from "./SLiderItem";
import "./About.scss";

class About extends React.Component {
    state = { items: [], slides: [], }

    mounted = true;

    async componentDidMount() {
        const { result, } = (await getRequest("/pages/about")).data;
        if (this.mounted) {
            this.setState({
                items: result.filter(item => item.img),
                slides: result.filter(item => !item.img),
            });
        }
    }

    componentWillUnmount() {
        this.mounted = false;
    }

    render() {
        if (!this.state.items.length) return null;
        const { showMobileAuth, } = this.props;
        const renderSlider = this.state.slides.map(slide => (
            <SliderItem
                key={slide.id}
                text={slide.text}
            />
        ));
        const renderItems = this.state.items.map((item, i) => (
            <AboutItem
                key={item.id}
                title={item.title}
                text={item.text}
                img={item.img}
                reverse={(i + 1) % 2 === 0}
            />
        ));

        return (
            <main className={classNames("page container", { "hide-element": showMobileAuth, })}>
                <div className="page__container">
                    <div className="about__list-container">
                        {renderItems}
                        <div className="slider-container">
                            {renderSlider}
                        </div>
                    </div>
                </div>
            </main>
        );
    }
}

About.propTypes = { showMobileAuth: PropTypes.bool.isRequired, };

export default About;
