import React from "react";
import { fbPixel, getRequest, addItemToCart, removeItemFromCart, getTotalCount, setCartPrice } from "./helpers";
import BasketMobile from "./icons/mobile/BasketMobile";
import MarketSection from "./MarketSectionComponent";


class Market extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            cartPrice: this.props.cartPrice,
            cartCount: getTotalCount(),
            categories: this.props.categories || [],
            categoriesData: [],
            cart: this.props.cart,
            recommendations: this.props.recommendations,
        };
    }

    componentDidMount() {
        this.processingCategories();
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.categories && nextProps.categories !== this.state.categories) {
            this.setState({ categories: nextProps.categories, });
            this.processingCategories();

            return true;
        } if (nextProps.cartPrice !== this.state.cartPrice) {
            this.setState({ cartPrice: nextProps.cartPrice, });

            return true;
        } if (nextProps.recommendations !== this.state.recommendations) {
            this.setState({ recommendations: nextProps.recommendations, });

            return true;
        } if (nextProps.cart !== this.state.cart) {
            this.setState({ cart: nextProps.cart, });

            return true;
        } if (nextState.categoriesData && nextState.categoriesData.length !== this.state.categoriesData.length) {
            this.setState({ categoriesData: nextState.categoriesData, });

            return true;
        } if (nextState.cartCount !== this.state.cartCount) {
            this.setState({ cartCount: nextState.cartCount, });

            return true;
        }

        return false;
    }

    openCart() {
        window.location.href = "#/basket";
    }

    async processingCategories() {
        const categoriesProcess = [];

        if (this.state.categories && this.state.categories.length) {
            categoriesProcess.push(this.state.categories[0]);
        }

        if (this.state.categories) {
            for (let i = 1; i < this.state.categories.length; i++) {
                if (this.state.categories[i].published === 1) {
                    let productsProcess = [];
                    await getRequest(`/products/category?category_id=${this.state.categories[i].id}&page=1`)
                        .then((res) => {
                            productsProcess = res.data.result.items.slice();
                        });

                    categoriesProcess.push({
                        id: this.state.categories[i].id,
                        title: this.state.categories[i].title,
                        active: false,
                        products: productsProcess,
                    });
                }
            }

            this.setState({ categoriesData: categoriesProcess, });
        }
    }

    render() {
        const self = this;

        return (
            <div className="container">
                {this.state.cartCount ? (
                    <div
                        className="mobile-basket-float"
                        onClick={() => {
                            this.openCart();
                        }}
                    >
                        <BasketMobile /><span className="basket__counter">{this.state.cartCount}</span>
                    </div>
                ) : ""}

                <div className="market-sections">
                    {this.state.categoriesData.map((item, i) => (
                        <MarketSection
                            key={i}
                            category={this.state.categoriesData[i]}
                            cart={this.state.cart}
                            addToCart={(product) => {
                                addItemToCart(product);
                                self.setState({ cartCount: getTotalCount(), });
                                setCartPrice();
                                fbPixel("micro");
                            }}
                            removeFromCart={(product) => {
                                removeItemFromCart(product);
                                self.setState({ cartCount: getTotalCount(), });
                                setCartPrice();
                            }}
                        />
                    ))
                    }
                </div>
            </div>
        );
    }
}

export default Market;
