import React from "react";
import classNames from "classnames";
import Scroll from "react-scroll";
import Helmet from "react-helmet";
import { fbPixel, getTotalPrice, getCart, setDeliveryMethod, formatCurrency, getBoulTitle } from "../helpers";
import AddIcon from "../icons/AddItem";
import RemoveIcon from "../icons/RemoveItem";
import RemoveItemButton from "../icons/RemoveItemButton";
import EmptyBasketIcon from "../icons/EmptyBasketIcon";
import MarketSection from "../MarketSectionComponent";

const scroll = Scroll.animateScroll;
const scrollOpt = {
    isDynamic: true,
    offset: -80,
    spy: true,
    smooth: true,
    duration: 500,
};

class BasketArea extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            cart: getCart(),
            cartPrice: getTotalPrice(),
            min_amount_for_delivery: this.props.min_amount_for_delivery,
            recommendations: this.props.recommendations,
        };
    }

    priceCart() {
        this.setState({ cartPrice: getTotalPrice(), });
    }

    get isDeliveryAvailable() {
        return (this.state.cartPrice === 0 || this.state.min_amount_for_delivery === 0 || this.state.cartPrice >= this.state.min_amount_for_delivery);
    }

    redirectToPayment(type) {
        setDeliveryMethod(type);
        this.props.onNavigateRequest("#/payment");
    }

    get basketItems() {
        return this.state.cart.map((item, i) => (
            <li key={"basket-item" + i} className="basket__item basket-pos js-basket-pos">
                <div className="basket-pos__header">
                    <div className="basket-pos__title">{item.itemData.title}</div>
                    <div className="basket-pos__price">
                        <span data-price={item.itemData.price}>{formatCurrency(item.itemData.price || item.itemData.amount)}</span>
                    </div>
                    <div className="basket-pos__consist">{item.itemData.description ? item.itemData.description : getBoulTitle(item.itemData.groups)}</div>
                    <div className="basket-pos__specifications">{item.itemData.specifications && item.itemData.specifications.map((prop, j) => (
                        <div
                            key={j}
                            className="basket-pos__prop"
                        >
                            {`${prop.value} ${prop.title}`}
                        </div>
                    ))}
                    </div>
                </div>
                <div className="basket-pos__footer">
                    <div
                        className="basket-pos__delete"
                        onClick={() => {
                            this.props.removeAllItemsFromCart(item.itemData);
                        }}
                    >
                        <RemoveItemButton />
                    </div>
                    <div className="basket-pos__amount amount-item">
                        <div
                            onClick={() => {
                                this.props.removeFromCart(item.itemData);
                            }}
                            className="amount-item__dec"
                        ><RemoveIcon />
                        </div>
                        <span className="amount-item__field">{item.numberItems}</span>
                        <div
                            onClick={() => {
                                this.props.addToCart(item.itemData);
                            }}
                            className="amount-item__inc"
                        ><AddIcon />
                        </div>
                    </div>
                </div>
            </li>
        ));
    }

    get basket() {
        return (
            <div className="basket-total__prices">
                <p className="basket-total__title">Стоимость заказа</p>
                <p className="basket-total__cost">
                    <span className="js-total-cost">{formatCurrency(this.state.cartPrice)}</span>
                </p>
            </div>
        );
    }

    get buttons() {
        return (
            <div className="basket-total__buttons">
                <button
                    className="basket-total__btn_dark btn-transparent"
                    onClick={() => {
                        this.redirectToPayment(1);
                        fbPixel("micro");
                    }}
                    type="button"
                >Самовывоз
                </button>
                <button
                    className="basket-total__btn_light btn-primary"
                    disabled={!this.isDeliveryAvailable}
                    onClick={() => {
                        this.redirectToPayment(2);
                        fbPixel("micro");
                    }}
                    type="button"
                >Доставка
                </button>
            </div>
        );
    }

    get items() {
        if (this.state.cart.length) {
            return (
                <ul className="basket__list">{this.basketItems}</ul>
            );
        }
        return (
            <div className="basket-empty">
                <div className="basket-empty__icon"><EmptyBasketIcon /></div>
                <div className="basket-empty__text">В корзине пока еще ничего нет</div>
                <div className="basket-empty__button">
                    <button
                        className="basket-total__btn_dark btn-transparent"
                        onClick={() => {
                            window.location.hash = "";
                        }}
                    >Вернуться в меню
                    </button>
                </div>
            </div>
        );
    }

    render() {
        return (
            <main className={classNames("page basket-page", { "hide-element": this.props.showMobileAuth, })}>
                <Helmet>
                    <title>{this.props.pageTitle}</title>
                </Helmet>
                <div className="page__container container">
                    <a className="page__return" href="#">
                        <svg className="svg svg-arrow-left ">
                            <use href="#arrow-left" />
                        </svg>
                    </a>
                    <p className="page__title">Ваш заказ</p>
                    <div className={classNames("basket-warning", { "hide-element": this.isDeliveryAvailable, })}>
                        <span className="basket-warning__icon">
                            <svg width="15" height="15" viewBox="0 0 11 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <circle cx="5.5" cy="6.5" r="5.1" stroke="#FD7065" strokeWidth="0.8" />
                                <path d="M5.95047 7.77445L6.13047 3.13945H5.05047L5.23047 7.77445H5.95047ZM5.00547 8.94445C5.00547 9.25945 5.27547 9.52945 5.59047 9.52945C5.90547 9.52945 6.17547 9.25945 6.17547 8.94445C6.17547 8.62945 5.90547 8.35945 5.59047 8.35945C5.27547 8.35945 5.00547 8.62945 5.00547 8.94445Z" fill="#FD7065" />
                            </svg>
                        </span>
                        <span className="basket-warning__text">
                            Минимальная сумма заказа {formatCurrency(this.state.min_amount_for_delivery)}. Вы можете заказать самовывоз или дополнить корзину.
                        </span>
                    </div>
                    <div className="basket">
                        {this.items}
                    </div>
                </div>
                <div className="container">
                    <div className="basket">
                        <MarketSection
                            category={{
                                id: 0,
                                title: "Рекомендуем",
                                products: this.state.recommendations,
                            }}
                            cart={this.state.cart}
                            addToCart={(item) => {
                                this.props.addToCart(item);
                                scroll.scrollToTop(scrollOpt);
                            }}
                            removeFromCart={this.props.removeFromCart}
                        />
                        <div className="basket__total-block basket-total">
                            {this.state.cart.length ? this.basket : ""}
                            {this.state.cart.length ? this.buttons : ""}
                        </div>
                    </div>
                </div>
            </main>
        );
    }
}

export default BasketArea;
