import React from 'react';
import Swiper from 'react-id-swiper';

class MarketSlides extends React.Component {
    /**
     * @constructor
     * Method for initialising data
     */
    constructor(props){
        super(props);
        this.state = {
            slides: this.props.slides || []
        };
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.slides !== this.state.slides) {
            this.setState({slides: nextProps.slides});
            return true;
        }

        return false;
    }

    render(){
        let items = this.state.slides.map((it, i) => <img key={'slide' + i} src={it.image} />);
        let params = {
            slidesPerView: 1,
            spaceBetween: 0,
            activeSlideKey: this.state.activeSlideIndex,
            loop: true,
            autoplay: {
                delay: 6000,
                disableOnInteraction: false
            },
            pagination: {
              el: '.swiper-pagination',
              clickable: true,
            },
            navigation: {
              nextEl: '.swiper-button-next',
              prevEl: '.swiper-button-prev',
            }
        };

        if (items.length === 0) {
            return '';
        }

        let cN = 'slide-container';
        if (this.props.moreCssClass) {
            cN = cN + ' ' + this.props.moreCssClass;
        }

        return (
            <div className={cN}>
                <Swiper {...params}>
                    {items}
                </Swiper>
            </div>
        );
    }
}

export default MarketSlides;
