import React from 'react';
import MarketItem from './MarketItemComponent';
import classNames from 'classnames';

class MarketSection extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    /**
     * Main method for render this component
     */
    render() {
        let items = [];
        for (let i = 0; i < this.props.category.products.length; i++) {
            let elementInCart = 0;
            this.props.cart.forEach((element) => {
                let elementId = element.itemData ? element.itemData.id : element.id;
                if (elementId === this.props.category.products[i].id) {
                    elementInCart = element.numberItems;
                }
            });
            items.push(<MarketItem
                key={'product' + this.props.category.products[i].id}
                product={this.props.category.products[i]}
                elementInCart={elementInCart}
                cart={this.props.cart}
                addToCart={this.props.addToCart}
                removeFromCart={this.props.removeFromCart}
                categoryID={this.props.category.id}
            />);
        }
        return (
            <section className="market-section" name={'section' + this.props.category.id}>
                <div className="market-section__title-container">
                    <div className="market-section__title">{this.props.category.title}</div>
                    <div className="market-section__title help-element"></div>
                </div>
                <div className={classNames({ 'swipe-container': this.props.category.id === 0 })}>
                    <div className={classNames("market-section__items", { 'scroll-container': this.props.category.id === 0 })}>
                        {items.map((item) => {
                            return item;
                        })}
                    </div>
                </div>
            </section>
        );
    }
}

export default MarketSection;
