import React from "react";
import classNames from "classnames";
import ym from "react-yandex-metrika";
import Logo from "../icons/LogoIcon";
import Menu from "../MenuComponent";
import Button from "../ButtonComponent";
import BurgerMobile from "../icons/mobile/Burger";
import basketIconDesctop from "./basket-icon-desctop.svg";
import basketIconMobile from "./mobile-basket-icon.svg";
import LogoMobile from "../icons/mobile/Logo";
import { formatCurrency, getCookie, fbPixel } from "../helpers";
import BonusIcon from "../icons/BonusIcon";
import ContentButton from "../ContentButton";

class Header extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            unfinishedOrder: getCookie("lastOrder"),
            mobileMenu: false,
            profile: this.props.profile,
            category: this.props.category,
        };
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.profile && nextProps.profile.phone !== this.state.profile.phone || nextProps.profile && nextProps.profile.bonuses !== this.state.profile.bonuses) {
            this.setState({ profile: nextProps.profile, });

            return true;
        } if (nextProps.categories !== this.state.categories) {
            this.setState({ categories: nextProps.categories, });

            return true;
        } if (nextProps.cartPrice !== this.state.cartPrice) {
            this.setState({ cartPrice: nextProps.cartPrice, });

            return true;
        } if (nextState.mobileMenu !== this.state.mobileMenu) {
            this.setState({ mobileMenu: nextState.mobileMenu, });

            return true;
        } if (nextProps.category !== this.state.category) {
            this.setState({ category: nextProps.category, });

            return true;
        } if (nextProps.showMobileAuth !== this.props.showMobileAuth) {
            return true;
        }

        return false;
    }

    componentDidMount() {
        window.addEventListener("scroll", this.listenToScroll);
    }

    componentWillUnmount() {
        window.removeEventListener("scroll", this.listenToScroll);
    }

    listenToScroll = () => {
        const winScroll = document.body.scrollTop || document.documentElement.scrollTop;
        const height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
        const scrolled = winScroll / height;

        this.setState({ mobileMenu: scrolled > 0.03, });
    }

    renderBasketIcon = ({ mobile, }) => {
        const basketIconClass = mobile ? "mobile-basket-icon" : "desctop-basket-icon";
        const basketIconSrc = mobile ? basketIconMobile : basketIconDesctop;
        return (
            <span
                className={classNames(basketIconClass, { "hide-element": !!this.state.unfinishedOrder === false, })}
                onClick={() => {
                    window.location.hash = "#/status/" + this.state.unfinishedOrder;
                }}
            >
                <img src={basketIconSrc} alt="Иконка статуса заказа" />
            </span>
        );
    }

    /**
     * Method for rendering Login button or Profile button
     */
    renderProfileButton() {
        if (this.state.profile.phone === undefined) {
            const title = "Войти";
            return (
                <ContentButton
                    buttonType="default"
                    buttonTitle={title}
                    buttonClass="profile-button"
                    type="button"
                    onClickMethod={() => {
                        this.props.onShowUserProfile();
                        fbPixel("micro");
                    }
                    }
                >
                    {title}
                </ContentButton>
            );
        }
        const title = `${this.state.profile.firstname || this.state.profile.phone} ${this.state.profile.bonuses}`;
        return (
            <ContentButton
                buttonType="default"
                buttonTitle={title}
                buttonClass="profile-button"
                onClickMethod={this.props.onShowUserProfile}
            >
                {title} <BonusIcon />
            </ContentButton>
        );
    }

    openCart() {
        console.log("cart");
        ym("reachGoal", "mobile_basket_click", { awesomeParameter: 42, });
        window.location.href = "#/basket";
    }

    render() {
        return (
            <header className={classNames("header", { "header-mobile": this.state.mobileMenu, })}>
                <div className="container">
                    <section className="top-level">
                        <div className="left-bar">
                            <a href="/"><Logo /></a>
                        </div>
                        <div className="right-bar">
                            <Menu
                                menuItems={[
                                    {
                                        link: "#/",
                                        title: "Меню",
                                    },
                                    {
                                        link: "#/about",
                                        title: "О нас",
                                    },
                                    {
                                        link: "#/sales",
                                        title: "Акции",
                                    },
                                    {
                                        link: "#/restaurants",
                                        title: "Рестораны",
                                    }
                                ]}
                            />
                            {this.renderBasketIcon({ mobile: false, })}
                            {this.renderProfileButton()}
                        </div>
                    </section>
                    <section className="bottom-level">
                        <div className="left-bar">
                            {/* <Button
                                buttonType="default"
                                buttonTitle="Собери боул"
                                buttonClass="boul-button"
                                onClickMethod={() => {
                                    window.location.href = "#/constructor";
                                }}
                            /> */}
                            <Menu
                                menuItems={this.props.categories}
                                menuItemClick={this.props.menuItemClick}
                                category={this.state.category}
                            />
                        </div>
                        <div className="right-bar">
                            <div className="price-container" id="cart-price">{formatCurrency(this.props.cartPrice)}</div>
                            <Button
                                buttonType="basket"
                                buttonTitle="Корзина"
                                onClickMethod={() => {
                                    window.location.href = "#/basket";
                                }}
                            />
                        </div>
                    </section>
                    <div className="mobile-section">
                        <div
                            className={classNames({ "hide-element": this.state.mobileMenu, })}
                            onClick={() => {
                                this.props.setShowMobileMenu();
                            }}
                        >
                            <BurgerMobile
                                open={this.props.showMobileAuth}
                                className={classNames({ "hide-element": this.state.mobileMenu, })}
                            />
                        </div>
                        <div className={classNames("boul-button__container", { "hide-element": !this.state.mobileMenu, })}>
                            {/* <Button
                                buttonType="default"
                                buttonTitle="Собери боул"
                                buttonClass="boul-button"
                                onClickMethod={() => {
                                    window.location.href = "#/constructor";
                                }}
                            /> */}
                            <Menu
                                menuItems={this.props.categories}
                                menuItemClick={this.props.menuItemClick}
                                category={this.state.category}
                            />
                        </div>
                        <a
                            className={classNames({ "hide-element": this.state.mobileMenu, })}
                            href="/"
                        >
                            <LogoMobile />
                        </a>
                        <div
                            className={classNames("right-bar", { "hide-element": this.state.mobileMenu, })}
                        >
                            {this.renderBasketIcon({ mobile: true, })}
                            <Button
                                className={classNames({ "hide-element": this.state.mobileMenu, })}
                                buttonType="basket"
                                buttonTitle={formatCurrency(this.props.cartPrice)}
                                buttonClass={classNames("mobile-basket-button", { "hide-element": this.state.mobileMenu, })}
                                onClickMethod={() => {
                                    this.openCart();
                                }}
                            />
                        </div>
                    </div>
                </div>
            </header>
        );
    }
}

export default Header;
