import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import { getRequest } from "../../helpers";
import "./Legal.scss";

class Legal extends React.Component {
    state = { page: "" }

    async componentDidMount() {
        const res = await getRequest("/pages/item?id=6");
        const page = res.data.result.description;
        this.setState({ page });
    }

    render() {
        return (
            <main className={classNames("page container", { "hide-element": this.props.showMobileAuth })}>
                <div className="page__container">
                    <div className="legal_agreement_container">
                        <div dangerouslySetInnerHTML={{ __html: this.state.page }} />
                    </div>
                </div>
            </main>
        );
    }
}

Legal.propTypes = { showMobileAuth: PropTypes.bool.isRequired };

export default Legal;
